body {
  margin: 0;
  
  font-family:'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/FilmFrenzy/static/media/background.9e6130f0.jpg) fixed no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.nav{
  padding: 1rem;
  padding-left: 3rem;
  padding-right: 3rem;
  display: flex;
  justify-content: space-between;
  font-size: 24px;
  background-color: rgb(128, 191, 188);
  color: white;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
}
.nav_link{
  text-decoration: none;
  color: white;
}
.header{
  margin-top: 3rem;
  margin-left: 3rem;
  margin-bottom: 2rem;
  font-size: 36px;
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
}
.movie_list{
    padding-left: 3rem;
    padding-right: 3rem;
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
}
.movie_card{
  margin-bottom: 2rem;
    width: 260px;
    text-align: left;
}
.card_thumb:hover{
  box-shadow: 1px 1px 10px #979797;
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}
/* .card_thumb:hover img{
  filter: brightness(0.5);
} */
.card_thumb{
  background: white;
  height: 360px;
  padding: 8px;
  border-radius: 18px;
}
.card_thumb img{
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  border-radius: 12px;
}
.card_title{
      margin-left: 10px;
    width: 260px;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    font-weight: 500;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.search_input{
  height: 35px;
  border-radius: 6px;
  width:200px;
  font-size: 14px;
  padding-left: 20px;
  outline:none;
  border: #ffffff;
}
.search_input:hover,:focus{
  transition:  0.3s ease-in-out;
  width: 350px;
}
.footer{
  display: flex;
  padding-left: 3rem;
  padding-right: 3rem;
  justify-content: space-between;
  align-items: center;
  margin-top: 5rem;
  background-color: rgb(64, 64, 64);
  height: 8rem;
  color: white;
  font-size: 24px;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
}













.modal_image{
  height: 100%;
  border-radius: 6px;
}
.modal_detail{
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* margin-right: 20px; */
}
.modalBackground {
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(200, 200, 200, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  z-index: 99;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal_container {
  font-family: 'Poppins', sans-serif;
  width: 60%;
  height: 70%;
  border-radius: 8px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 6px 12px;
  display: flex;
  flex-direction: row;
  padding: 20px;
  /* box-sizing: border-box; */
}

.modal_container .title {
  display: inline-block;
  /* text-align: left; */
  padding-right: 20px;
  font-size: 18px;
}

.titleCloseBtn {
  display: flex;
  justify-content: flex-end;
}

.titleCloseBtn img {
  background-color: transparent;
  border: none;
  font-size: 25px;
  cursor: pointer;
}

.modal_container .body {
  font-size: 1rem;
  font-weight: 500;
  margin-right: 20px;
}

.trailer_button{
  width: 150px;
  height: 45px;
  border: none;
  color: white;
  border-radius: 8px;
  font-size: 18px;
  cursor: pointer;
  background-color: crimson;
}
